import React, { useState, useEffect } from 'react';
import * as musicMetadata from 'music-metadata-browser';


const EachTone = ({ folderUrl, playSong, updatePlayer }) => {
  const [songName, setSongName] = useState('');
  const [artist, setArtist] = useState('');
  const [album, setAlbum] = useState('');
  const defaultImageUrl = "https://images.unsplash.com/photo-1521133573892-e44906baee46?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=934&q=80";
  const [imageUrl, setImageUrl] = useState(defaultImageUrl);

  useEffect(() => {
    if (folderUrl) {
      fetch(folderUrl)
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          return response.blob();
        })
        .then(blob => {
          return musicMetadata.parseBlob(blob);
        })
        .then(metadata => {
          const { common, format } = metadata;
  
          setSongName(common.title || 'Unknown Title');
          setArtist(common.artist || 'Unknown Artist');
          setAlbum(common.album || 'Unknown Album');
  
          // Check if there is a picture, otherwise set default image
          if (common.picture && common.picture.length > 0) {
            const imageBlob = new Blob([common.picture[0].data], { type: common.picture[0].format });
            const reader = new FileReader();
            reader.onloadend = () => {
              setImageUrl(reader.result);
            };
            reader.readAsDataURL(imageBlob);
          } else {
            setImageUrl(defaultImageUrl);
          }
  
          console.log('Format:', format);
        })
        .catch(error => console.error('Error fetching audio file:', error));
    }
  }, [folderUrl]);

  const handleClick = () => {
    playSong(folderUrl);
    updatePlayer({ songName, artist, album, imageUrl });
  };

  return (
    <div className="song" onClick={handleClick}>
      <div className="song-img">
        <img src={imageUrl} alt="album cover" />
      </div>
      <div className="song-detail">
        <div className="song-name">{songName}</div>
        <div className="artist">{artist}</div>
        <div className="album">{album}</div>
      </div>
    </div>
  );
};

export default EachTone;
